<template>
  <div>
    <div id="layout" class="d-flex flex-column">
      <main>
        <div class="content_wrapper">
          <div class="menu_wrapper container-fluid">
            <div class="itdmavatar">
              <section class="avatar_brick">
                <!-- <div><img src="img/logo.svg" class="avatar_photo img-circle img-responsive" /></div> -->

                <div class="text-center">
                  <label class="user">{{ currentuser.fullname }}</label>
                </div>
                <div class="form-group">
                  <select
                    class="form-control"
                    id="exampleFormControlSelect1"
                    v-model="seldeptname"
                  >
                    <option selected>--請選擇組別--</option>
                    <option v-for="(item, index) in deptname" :key="index">{{
                      item
                    }}</option>
                  </select>
                </div>
                <div class="text-center" :class="{ hidden: isShow }">
                  <label class="user">{{ seldate }}</label>
                </div>
              </section>
            </div>
            <div
              class="menu col col-sm-8 col-md-7  col-lg-6 col-xl-5"
              style="grid-template: repeat(1,1fr)/repeat(1,1fr);"
            >
              <section class="menu_brick">
                <i class="fa fa-check-square-o" aria-hidden="true"></i>
                <label for="">打卡</label>
              </section>
            </div>
            <div class="menu col col-sm-8 col-md-7  col-lg-6 col-xl-5">
              <section class="menu_brick" @click.prevent="openselmenu('D')">
                <i class="fa fa-check-square-o" aria-hidden="true"></i>
                <label for="">日巡檢</label>
              </section>
              <section class="menu_brick" @click.prevent="openselmenu('W')">
                <i class="fa fa-cloud-upload" aria-hidden="true"></i>
                <label for="">周巡檢</label>
              </section>
              <section class="menu_brick" @click.prevent="openselmenu('M')">
                <i class="fa fa-cloud-download" aria-hidden="true"></i>
                <label for="">月巡檢</label>
              </section>
            </div>
            <div
              class="menu col col-sm-8 col-md-7  col-lg-6 col-xl-5"
              style="grid-template: repeat(1,1fr)/repeat(1,1fr);"
              :class="{ hidden: isShow }"
            >
              <!-- <section class="menu_brick" @click.prevent="getlog"> 
                            <i class="fa fa-check-square-o" aria-hidden="true"></i>
                            <label for="">下載巡檢項目</label>
                        </section> -->
              <section class="menu_brick" @click.prevent="carryout">
                <i class="fa fa-check-square-o" aria-hidden="true"></i>
                <label for="">執行巡檢作業</label>
              </section>
              <!-- <section class="menu_brick" @click.prevent="SendLog">
                            <i class="fa fa-check-square-o" aria-hidden="true"></i>
                            <label for="">上傳巡檢結果</label>
                        </section> -->
            </div>
            <div
              class="error-message alert text-center"
              style="position: fixed;margin-bottom: 0;"
              :class="{ hidden: prompt }"
            >
              {{ prompttext }}
            </div>
          </div>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import getapilink from '@/assets/js/api/GetApiLink';
export default {
  data() {
    return {
      seldeptname: "",
      periodtype: "",
      prompttext: "",
      prompt: true,
      isShow: true,
      seldate: "目前選擇：",
    };
  },
  methods: {
    openselmenu(payload) {
      const vm = this;
      vm.periodtype = payload;
      if (!vm.seldeptname) {
        vm.prompttext = "請選擇組別";
        vm.prompt = false;
      } else {
        switch (payload) {
          case "D":
            vm.seldate = "目前選擇：日巡檢";
            break;

          case "W":
            vm.seldate = "目前選擇：週巡檢";
            break;

          case "M":
            vm.seldate = "目前選擇：月巡檢";
            break;
        }
        vm.isShow = false;
      }
      vm.getlog();
    },
    getlog(payload) {
      const vm = this;
      if (vm.periodtype === "") {
        vm.prompttext = "請選擇巡檢方式";
        vm.prompt = false;
      } else {
        const nowtime = new Date(+new Date() + 8 * 3600 * 1000)
          .toISOString()
          .split("T");
        const data = {
          deptname: vm.seldeptname,
          periodtype: vm.periodtype,
          logdate: nowtime[0].replace(/-/g, "/"),
        };
        const getlogapi = getapilink + "itdmlimsapi/Inspect/GetLog";
        this.$http
          .post(getlogapi, data)
          .then((response) => {
            vm.prompttext = "下載成功";
            vm.prompt = false;
            vm.depositdata(response.data);
          })
          .catch((error) => {
            vm.prompttext = "下載失敗";
            vm.prompt = false;
          });
      }
    },
    carryout() {
      const vm = this;
      if (vm.prompttext === "下載成功") {
        vm.$router.push("/ScanList");
      } else {
        vm.prompttext = "請下載巡檢項目";
        vm.prompt = false;
      }
    },
    SendLog() {
      const vm = this;
      const sendlogapi = getapilink + "itdmlimsapi/Inspect/SendLog";
      const data = [];
      for (const i in vm.scanlist) {
        if (!vm.scanlist[i].result) {
        } else {
          let temp = {};
          temp.inspectid = vm.scanlist[i].inspectid;
          temp.result = vm.scanlist[i].result;
          temp.complete = vm.scanlist[i].complete;
          temp.createts = vm.scanlist[i].createts;
          temp.createuserid = vm.scanlist[i].createuserid;
          temp.inspect_logid = vm.scanlist[i].inspect_logid;
          data.push(temp);
        }
      }

      // data[0].inspectid = vm.scanlist[1].inspectid;
      // data[0].result = 'Y';
      // data[0].complete = 'Y';
      // data[0].createts = '2020-12-08T21:55:29';
      // data[0].createuserid = '118';
      // data[0].inspect_logid = vm.scanlist[1].inspect_logid;
      console.log(data);
      this.$http.post(sendlogapi, data).then((response) => {
        console.log(response);
        vm.prompttext = "上傳成功";
        vm.prompt = false;
        data = [];
      });
      // .catch((error) => {
      //     vm.prompttext = '上傳失敗';
      //     vm.prompt = false;
      // })
    },
    ...mapActions(["depositdata"]),
  },
  watch: {
    prompt: function() {
      const vm = this;
      setTimeout(() => {
        vm.prompt = true;
      }, 3000);
    },
  },
  computed: {
    ...mapGetters(["currentuser", "deptname", "scanlist"]),
  },
  created() {},
};
</script>
